/* @import url('https://fonts.googleapis.com/css?family=Roboto+Condensed&display=swap'); */

.App {
  position: fixed;
  top: -11vh;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100vh;
  text-align: center;
  background: linear-gradient(0deg, rgba(49, 49, 177, 1) 0%, rgba(49, 49, 177, 1) 50%, rgba(49, 49, 177, 0.6) 100%);
}

.container {
  width: 100%;
  margin: 25vh auto;
}
.col-4 {
  width: 25%;
  float: left;
}

h1 {
  font-size: 38px;
  text-transform: uppercase;
  line-height: 1;
  color: #FFB237;
  margin-bottom: 1em;
}

@media (min-width: 768px) {
  .container {
    width: 1100px;
  }
  h1 {
    font-size: 58px;
  }
}
